.App {
  text-align: center;
  background: linear-gradient(135deg, #f0f0f0, #ff8c6c); /* Grey to orange gradient */
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-family: Arial, sans-serif;
}

.App-header {
  background: rgba(255, 255, 255, 0.9); /* Light background to contrast with chat icon */
  padding: 2rem;
  border-radius: 12px;
  box-shadow: 0 4px 8px rgba(0, 0, 0, 0.3);
  color: #000;
  text-align: center;
  position: relative;
  z-index: 1; /* Make sure the header content is on top */
}

.title {
  font-size: 2.5rem;
  font-weight: bold;
  margin-bottom: 1rem;
  color: #FF4F1F; /* Accent color */
}

.description {
  font-size: 1.2rem;
  margin-bottom: 1rem;
}

.instructions {
  font-size: 1rem;
  margin-bottom: 2rem;
}

/* Messenger Styles */
.g-messenger-widget .g-messenger-header {
  background-color: #0070AD;
  color: white;
}

.g-messenger-widget .g-messenger-button {
  background-color: #12ABDB;
  border-radius: 50%;
}
